.main-footer {
  background-color: $primary;
  padding: 50px 0 0;
  &__bottom {
    border-bottom: 1px solid #fff;
    padding-bottom: 20px;
    font-family: $ff-title;
    font-weight: 300;
    font-size: 13px;
    line-height: 1.5;
    text-align: center;
    color: #fff;
    @include lg-max {
      margin: 0 30px;
    }
  }
  &__logo {
    margin: 0 auto 20px;
  }
  &__rights {
    padding: 29px 20px;
    font-family: $ff-title;
    font-weight: 300;
    font-size: 13px;
    line-height: 1.5;
    text-align: center;
    color: #fff;
    @include lg-max {
      margin: 0 30px;
    }
  }
  .socials {
    color: #fff;
    @include lg-max {
      position: absolute;
      bottom: 48px;
      left: 50%;
      transform: translate(-50%, 0);
    }
    &__title {
      text-align: center;
      display: none;
      @include lg-max {
        display: block;
      }
    }
    &__list li {
      margin: 0 10px;
    }
    &__link {
      color: #fff;
      &:hover {
        color: $secondary;
      }
    }
  }
}
.contact-form {
  padding: 0 10px 47px;
  width: 100%;
  max-width: 606px;
  margin: 0 auto;
  &__title {
    margin-bottom: 26px;
    font-family: $ff-title;
    font-weight: 300;
    font-size: 36px;
    color: #fff;
    text-transform: uppercase;
    text-align: center;
    @include lg-max {
      font-size: 30px;
    }
    @include sm-max {
      font-size: 20px;
    }
  }
  .btn[type="submit"] {
    margin: 5px 13px;
    @include lg-max {
      margin: 5px;
    }
  }
}
.footer-navigation {
  width: 100%;
  max-width: 1078px;
  margin: 0 auto;
  padding: 0 30px 70px;
  display: flex;
  justify-content: space-between;
  color: #fff;
  font-family: $ff-text;
  font-weight: 300;
  font-size: 14px;
  line-height: 1.5;
  position: relative;
  @include lg-max {
    padding-bottom: 160px;
    flex-direction: column;
  }
  &__item {
    margin-left: 20px;
    @include lg-max {
      margin-left: 0;
    }
    &:first-child {
      margin-left: 0;
      min-width: 30%;
    }
    &:not(:last-child){
      @include lg-max {
        border-bottom: 1px solid #fff;
      }
    }
  }
  &__title {
    margin-bottom: 26px;
    font-family: $ff-title;
    font-weight: 300;
    font-size: 20px;
    line-height: 1.2;
    position: relative;
    @include lg-max {
      margin: 0;
      padding: 15px 0;
    }
    &::after,
    &::before {
      content: "";
      width: 18px;
      height: 1px;
      background-color: #fff;
      position: absolute;
      right: 0;
      top: 50%;
      display: none;
      transition: all .3s ease;
      @include lg-max {
        display: block;
      }
    }
    &::after {
      transform: rotate(90deg);
    }
    &.active:after {
      transform: rotate(0);
    }
  }
  &__list {
    list-style: none;
    @include lg-max {
      display: none;
    }
    li {
      margin-bottom: 17px;
    }
  }
}
.footer-nav {
  display: flex;
  justify-content: space-between;
  @include lg-max {
    justify-content: flex-start;
  }
  @include sm-max {
    flex-direction: column;
  }
  &__list {
    padding-right: 20px;
    list-style: none;
    @include lg-max {
      min-width: 200px;
    }
    li {
      margin-bottom: 10px;
    }
  }
  &__link {
    color: #fff;
    position: relative;
    &::before {
      content: "";
      width: 0;
      height: 1px;
      background-color: #fff;
      position: absolute;
      top: 100%;
      left: 0;
      transition: all 0.3s ease;
    }
    &:hover::before {
      width: 100%;
    }
  }
}
