.services {
  margin-top: 38px;
  padding: 78px 0 122px;
  position: relative;
  @include xxl-max {
    overflow-x: hidden;
  }
  @include md-max {
    padding-top: 0;
    padding-bottom: 0;
    margin-top: 46px;
  }
  &__decor {
    max-width: 50%;
    position: absolute;
    top: 0;
    right: 0;
    width: 450px;
    height: 450px;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: right top;
    @include xxl-max {
      right: -50px;
    }
    @include md-max {
      width: 215px;
      height: 215px;
    }
    @include sm-max {
      right: -107px;
    }
  }
  &__content {
    min-height: 360px;
    @include xxl-max {
      max-width: 50%;
    }
    @include md-max {
      width: 100%;
      max-width: 100%;
    }
  }
  &__text {
    padding-top: 26px;
    max-width: 70%;
    font-family: $ff-text;
    font-size: 20px;
    font-weight: 300;
    line-height: 1.5;
    @include xxl-max {
      max-width: 383px;
    }
    @include md-max {
      font-size: 18px;
      padding-top: 29px;
      max-width: 100%;
    }
  }
  &__top {
    display: flex;
    flex-direction: column;
    justify-content: center;
    @include md-max {
      max-width: calc(100% - 215px);
      min-height: 215px;
    }
    @include sm-max {
      max-width: calc(100% - 80px);
    }
  }
  .main-title {
    margin-bottom: 5px;
  }
  &__list {
    padding-top: 49px;
  }
}
.service-list {
  list-style: none;
  display: flex;
  align-items: flex-start;
  justify-content: space-around;
  text-align: center;
  @include md-max {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  &__item {
    padding: 0 10px;
    @include md-max {
      margin-bottom: 46px;
    }
  }
  &__number {
    margin: 0 auto 20px;
    width: 144px;
    height: 144px;
    background-image: url(../img/design-number.png);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 44px;
    font-weight: 300;
    color: rgba(0, 0, 0, 0.2);
    @include sm-max {
      margin-bottom: 15px;
      width: 116px;
      height: 116px;
    }
  }
  &__title {
    font-family: $ff-title;
    font-size: 24px;
    font-weight: 500;
    text-transform: uppercase;
    text-align: center;
    @include sm-max {
      font-size: 20px;
    }
  }
}
.premium-services {
  padding: 35px 0 50px;
  @include lg-max {
    position: relative;
    padding-top: 40px;
    padding-bottom: 133px;
  }
  &__grid {
    display: flex;
    @include lg-max {
      flex-direction: column;
    }
  }
  &__btn {
    @include lg-max {
      padding: 0 20px;
      position: absolute;
      bottom: 65px;
      left: 0;
      right: 0;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
  &__top {
    margin-right: 20px;
    width: 44%;
    @include lg-max {
      width: 100%;
      text-align: center;
    }
  }
  &__list {
    width: 56%;
    @include lg-max {
      width: 100%;
    }
  }
  .main-subtitle {
    margin-bottom: 50px;
    @include lg-max {
      margin-bottom: 30px;
    }
  }
}
.premium-service {
  display: flex;
  @include sm-max {
    flex-direction: column;
  }
  &__icon {
    flex-shrink: 0;
    width: 83px;
    margin-right: 23px;
    @include sm-max {
      width: 58px;
      margin-bottom: 13px;
    }
  }
  &__text {
    line-height: 23px;
  }
  &__title {
    margin-bottom: 6px;
    font-family: $ff-title;
    font-size: 24px;
    line-height: 29px;
    font-weight: 500;
    text-transform: uppercase;
    @include md-max {
      font-size: 20px;
    }
  }
}
