.faq-header {
  padding-top: 47px;
  padding-bottom: 70px;
  display: flex;
  align-items: flex-end;
  position: relative;
  @include sm-max {
    min-height: 315px;
    padding-bottom: 0;
  }

  &__img {
    max-width: 50%;
    @include sm-max {
      width: 50%;
      position: relative;
    }
    img {
      @include sm-max {
        position: absolute;
        right: -30px;
        bottom: 0;
        height: 300px;
        width: 366px;
        max-width: none;
        z-index: -1;
      }
    }
  }
  &__content {
    width: 50%;
    flex-grow: 1;
    flex-direction: column;
    display: flex;
    align-items: center;
    margin-bottom: 45px;
    justify-content: end;
  }
}
.faq-block {
  padding: 60px 0 70px;
  &__title {
    margin-bottom: 60px;
    font-family: $ff-title;
    font-size: 24px;
    line-height: 29px;
    text-transform: uppercase;
    font-weight: 500;
    @include sm-max {
        font-size: 20px;
        line-height: 24px;
    }
  }
  &__item {
    border-top: 1px solid $primary;
    &:last-child {
      border-bottom: 1px solid $primary;
    }
  }
  &__trigger {
    padding: 23px 20px 23px 0;
    font-family: $ff-title;
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    display: block;
    position: relative;
    &:after {
      content: "";
      width: 15px;
      height: 15px;
      background-color: transparent;
      border-left: 1px solid #3a2c48;
      border-bottom: 1px solid #3a2c48;
      position: absolute;
      right: 0;
      top: 50%;
      transform: translate(0, -50%) rotate(-45deg);
      cursor: pointer;
      transition: all 0.3s ease;
    }
    &:hover::after {
      border-color: $primary;
    }
  }
  &__inner {
    padding-top: 5px;
    padding-bottom: 35px;
    max-width: 785px;
    font-family: $ff-text;
    font-weight: 300;
    font-size: 18px;
    line-height: 27px;
    color: #707070;
  }
  &--dark {
    background-color: $secondary;
    color: #fff;
    .faq-block__trigger::after {
      border-color: #fff;
    }
    .faq-block__inner {
      color: #fff;
    }
  }
}
