.not-found {
  padding: 66px 0 98px;
  background-image: url(../img/design4.png);
  background-position: center 60px;
  background-size: 50%;
  background-repeat: no-repeat;
  text-align: center;
  @include md-max {
    padding: 105px 0 60px;
    background-size: 260px;
    background-position: center 55px;
  }
  &__number {
      font-family: $ff-text;
      font-size: 232px;
      line-height: 294px;
      font-weight: 500;
      text-align: center;
      color: $primary;
      @include md-max {
          font-size: 114px;
          line-height: 171px;
      }
  }
  &__text {
      margin-bottom: 43px;
      font-family: $ff-title;
      font-weight: 300;
      font-size: 36px;
      line-height: 42px;
      text-align: center;
      @include md-max {
          font-size: 24px;
          line-height: 29px;
          margin-bottom: 88px;
      }
      strong {
          font-weight: 500;
      }
  }
}
