.security {
  &__header {
    margin-bottom: 100px;
    @include lg-max {
        margin-bottom: 63px;
    }
  }
  &__title {
    margin-bottom: 25px;
    max-width: 500px;
    font-family: $ff-title;
    font-weight: 500;
    font-size: 44px;
    line-height: 1.16;
    color: #000;
    text-transform: uppercase;
    position: relative;
    display: flex;
    align-items: center;
    @include lg-max {
      max-width: 450px;
      font-size: 36px;
    }
    @include sm-max {
      max-width: 100%;
      padding-right: 10px;
      font-size: 26px;
    }
  }
  &__number {
    min-width: 80px;
    font-family: "Raleway", "Arial", sans-serif;
    font-size: 115px;
    font-weight: 300;
    color: #9c8f63;
    display: block;
    margin-top: -25px;
    @include sm-max {
      min-width: 50px;
      font-size: 70px;
    }
  }
  &__list {
    margin-bottom: 118px;
    list-style: none;
    @include lg-max {
        margin-bottom: 70px;
    }
  }
  &__item {
    display: flex;
    align-items: center;
    &:not(:last-child) {
      margin-bottom: 245px;
      @include lg-max {
        margin-bottom: 73px;
    }
    }
    @include lg-max {
        flex-direction: column;
    }
  }
  &__img {
    width: 50%;
    @include lg-max {
        margin-bottom: 25px;
        width: 100%;
        order: -1;
    }
    &--right {
      text-align: right;
      @include lg-max {
        text-align: left;
    }
    }
    img {
        @include lg-max {
            max-width: 100px;
        }
    }
  }
  &__content {
    width: 50%;
    font-family: $ff-text;
    font-size: 20px;
    font-weight: 300;
    line-height: 1.5;
    @include lg-max {
        width: 100%;
    }
    @include sm-max {
        font-size: 18px;
    }
  }
  &__subtitle {
    margin-bottom: 6px;
    font-family: $ff-title;
    font-size: 24px;
    font-weight: 500;
    text-transform: uppercase;
  }
  &__text {
    display: block;
    max-width: 500px;
    font-family: $ff-text;
    font-size: 20px;
    font-weight: 300;
    line-height: 1.5;
    color: #000;
    @include sm-max {
      max-width: 100%;
        font-size: 18px;
    }
  }
}
.security-hero {
  padding: 26px 0 52px;
  display: flex;
  align-items: center;
  @include md-max {
      flex-direction: column-reverse;
  }
  &__content {
    width: 50%;
    @include md-max {
        width: 100%;
    }
  }
  &__img {
    width: 50%;
    @include md-max {
        text-align: center;
        width: 100%;
    }
  }
  &__title {
    font-family: $ff-title;
    font-size: 60px;
    font-weight: 300;
    max-width: 391px;
    color: $primary;
    position: relative;
    z-index: 2;
    @include lg-max {
        font-size: 50px;
    }
    @include md-max {
        text-align: center;
        margin-left: auto;
        margin-right: auto;
        font-size: 38px;
    }
  }
}
