.dark-section {
  padding: 50px 0;
  background-color: $secondary;
  @include md-max {
      padding: 28px 0;
  }
  &__header {
    margin-bottom: 50px;
    @include md-max {
        margin-bottom: 30px;
    }
  }
  &__block {
    margin-bottom: 24px;
    .main-subtitle {
      margin-bottom: 24px;
      @include md-max {
          margin-bottom: 6px;
      }
    }
  }
  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
