.article {
  padding: 60px 0 70px;
  display: flex;
  flex-direction: column;
  @include lg-max {
    padding-bottom: 0;
  }
  &__name {
      margin-bottom: 6px;
      font-family: $ff-title;
      font-size: 36px;
      font-weight: 500;
      text-transform: uppercase;
      @include sm-max {
          font-size: 26px;
      }
      
  }
  &__img {
    margin-bottom: 50px;
    width: 100%;
    padding-bottom: 43%;
    border-radius: 20px;
    overflow: hidden;
    position: relative;
    @include lg-max {
      margin-bottom: 40px;
    }
    @include sm-max {
      padding-bottom: 100%;
    }
    img {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__title {
    margin-bottom: 23px;
    padding-right: 390px;
    @include lg-max {
      padding-right: 0;
      order: -1;
    }
  }
  &__body {
    padding-right: 390px;
    position: relative;
    @include lg-max {
      padding-right: 0;
    }
  }
  &__content {
    line-height: 23px;
  }
  &__navigation {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__author {
    font-family: $ff-title;
    font-size: 20px;
    font-weight: 300;
    line-height: 24px;
    color: $primary;
  }
  .line-block {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}
