.main-header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  padding: 18px 90px;
  display: flex;
  align-items: center;
  z-index: 99;
  @include lg-max {
    padding: 20px;
  }
  &--dark {
    position: relative;
    background-color: $secondary;
  }
  &__logo {
    margin-right: 20px;
    min-height: 5.4rem;
    flex-shrink: 0;
    z-index: 99;
    transition: all 0.3s ease;
    @include lg-max {
      min-height: 5rem;
    }
  }
}
.main-nav {
  margin-left: auto;
  &__list {
    margin-left: auto;
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    flex-wrap: wrap;
    li {
      margin: 5px 0;
    }
  }
  &__link {
    margin-right: 25px;
    color: $accent;
    font-family: $ff-title;
    font-size: 1.6rem;
    font-weight: 300;
    transition: all 0.3s ease;
    cursor: pointer;
    position: relative;
    &::before {
      content: "";
      position: absolute;
      top: 100%;
      left: 0;
      right: 0;
      margin: 0 auto;
      width: 0;
      height: 1px;
      background-color: $accent;
      transition: all 0.3s ease;
    }
    &:hover::before {
      width: 100%;
    }
  }
  &__footer {
    display: none;
    position: fixed;
    left: 20px;
    bottom: 30px;
    .icon-call {
      margin-right: 6px;
      font-size: 17px;
    }
  }
  &__close {
    display: none;
    position: absolute;
    right: 25px;
    top: 25px;
    cursor: pointer;
    span {
      display: block;
      width: 30px;
      height: 30px;
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
    }
    & ::after {
      content: "";
      width: 100%;
      height: 1px;
      background-color: $accent;
      transform: rotate(45deg);
      position: absolute;
    }
    & ::before {
      content: "";
      width: 100%;
      height: 1px;
      background-color: $accent;
      transform: rotate(-45deg);
      position: absolute;
    }
  }
  .socials {
    display: none;
  }
}
.socials {
  &__title {
    margin-bottom: 18px;
    font-family: $ff-title;
    font-size: 20px;
    font-weight: 300;
    text-transform: uppercase;
    line-height: 1.2;
  }
  &__list {
    list-style: none;
    display: flex;
    align-items: center;
    li {
      margin-right: 20px;
    }
  }
  &__link {
    color: #000;
    font-size: 24px;
    transition: all 0.3s ease;
    cursor: pointer;
    &:hover {
      color: $accent;
    }
  }
}
.burger-menu {
  display: none;
  flex-shrink: 1;
  &__btn {
    position: relative;
    width: 35px;
    height: 35px;
    display: flex;
    align-items: center;
    cursor: pointer;
  }
  &__line {
    margin-right: 10px;
    display: block;
    width: 25px;
    height: 1px;
    background-color: $accent;
    &::before,
    &::after {
      content: "";
      position: absolute;
      width: 100%;
      height: 1px;
      background-color: $accent;
    }
    &::before {
      top: 8px;
    }
    &::after {
     bottom: 7px;
    }
  }
}
@include xl-max {
  .main-header {
    height: 88px;
    justify-content: space-between;
    &__logo {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      &.active-menu {
        left: 20px;
        transform: translate(0, -50%);
      }
    }
  }
  .burger-menu {
    display: block;
  }
  .main-nav {
    min-width: 500px;
    padding: 100px 20px;
    height: 100vh;
    max-height: 100vh;
    position: absolute;
    top: 0;
    bottom: 0;
    left: -100vw;
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease;
    display: flex;
    flex-direction: column;
    &.active {
      opacity: 1;
      visibility: visible;
      left: 0;
      background-color: #fff;
      z-index: 98;
    }
    &__list {
      margin-left: 0;
      max-height: calc(100vh - 200px);
      overflow-y: auto;
      flex-direction: column;
      align-items: flex-start;
      justify-content: flex-start;
      flex-wrap: nowrap;
      li:not(:last-child) {
        margin: 0 0 26px;
      }
    }
    &__link {
      color: #000;
      font-size: 2rem;
      &:hover {
        color: $accent;
      }
      &::before {
        display: none;
      }
    }
    &__footer {
      display: block;
    }
    &__close {
      display: block;
    }
    .socials {
      margin-top: 28px;
      display: block;
    }
  }
}
@include sm-max {
  .main-nav {
    min-width: 300px;
  }
}
