.container {
  width: 100%;
  max-width: 1076px;
  margin: 0 auto;
  padding: 0 20px;
}
.container-md {
  width: 100%;
  max-width: 1126px;
  margin: 0 auto;
  padding: 0 20px;
}
.main-title {
  margin: 0 0 7px;
  font-family: $ff-title;
  font-size: 44px;
  line-height: 1.2;
  font-weight: 500;
  text-transform: uppercase;
  @include lg-max {
    font-size: 40px;
  }
  @include sm-max {
    font-size: 26px;
  }
  &--white {
    color: white;
  }
}
.main-subtitle {
  margin: 0 0 7px;
  font-family: $ff-title;
  font-size: 36px;
  line-height: 1.2;
  font-weight: 300;
  text-transform: uppercase;
  @include lg-max {
    font-size: 30px;
  }
  @include sm-max {
    font-size: 20px;
  }
  &--white {
    color: white;
  }
  strong {
    font-weight: 500;
  }
}
.main-text {
  font-family: $ff-text;
  font-size: 20px;
  line-height: 1.5;
  color: #000;
  font-weight: 300;
  @include sm-max {
    font-size: 18px;
  }
  p {
    padding-bottom: 23px;
  }
}
.btn {
  min-width: 189px;
  display: inline-block;
  padding: 16px 34px;
  border-radius: 26px;
  font-family: $ff-text;
  font-weight: 300;
  font-size: 15px;
  line-height: 1.2;
  color: #fff;
  border: 1px solid $primary;
  background-color: $primary;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
  transition: all 0.3s ease;
  @include sm-max {
    min-width: 100px;
  }
  &:hover {
    transform: translate(1px, -1px);
    box-shadow: 2px 2px 4px 1px rgba(0, 0, 0, 0.2);
  }
  &--outline {
    background-color: transparent;
  }
  &--white {
    background-color: #fff;
    color: #000;
  }
  &--full {
    width: 100%;
  }
  &--lg {
    min-width: 335px;
    @include sm-max {
      min-width: auto;
      width: 100%;
    }
  }
  [class^="icon-"] {
    margin-right: 8px;
  }
}
.link-more {
  display: inline-block;
  font-family: $ff-title;
  font-weight: 300;
  font-size: 15px;
  line-height: 1.2;
  color: $primary;
  text-transform: uppercase;
  transition: all 0.3s ease;
  &::after {
    content: ">>";
    margin-left: 5px;
    transition: all 0.3s ease;
  }
  &:hover:after {
    margin-left: 15px;
  }
}
.text-white {
  color: white;
}
.text-primary {
  color: $primary;
}
.posr {
  position: relative;
}

.tac {
  text-align: center;
}

.ttu {
  text-transform: uppercase;
}

.df {
  display: flex;
}

.ovh {
  overflow: hidden;
}

.aic {
  align-items: center;
}

.jcc {
  justify-content: center;
}

.jcsb {
  justify-content: space-between;
}

.jcsa {
  justify-content: space-around;
}

.flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.m0 {
  margin: 0 !important;
}

.order-first {
  order: -1;
}

.order-first-sm {
  @include sm {
    order: -1;
  }
}

.order-first-md {
  @include md {
    order: -1;
  }
}

.order-first-lg {
  @include lg {
    order: -1;
  }
}

.order-first-xl {
  @include xl {
    order: -1;
  }
}

.order-first-xxl {
  @include xxl {
    order: -1;
  }
}

