.large-banner {
  min-height: 678px;
  display: flex;
  align-items: flex-end;
  position: relative;
  &__img {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    height: 100%;
    width: 100%;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: right;
    }
  }
  &__content {
    flex-grow: 1;
    width: 100%;
    max-width: 1040px;
    margin: 0 auto;
    padding: 100px 20px 50px;
    z-index: 2;
    @include md-max {
      padding: 50px 20px 33px;
    }
  }
  &__title {
    max-width: 610px;
    margin-bottom: 26px;
    font-family: $ff-title;
    font-size: 44px;
    font-weight: 300;
    color: #fff;
    text-transform: uppercase;
    @include md-max {
      max-width: 310px;
      font-size: 22px;
    }
  }
}
