.blog {
    &__grid {
        margin: 40px -20px;
        display: flex;
        flex-wrap: wrap;
        @include sm-max {
            margin: 30px 0 20px;
        }
    }
    &__item {
        padding: 40px 20px;
        width: 33.33333%;
        @include lg-max {
            width: 50%;
        }
        @include sm-max {
            padding: 25px 0;
            width: 100%;
        }
    }
}
.blog-card {
    width: 100%;
    &__img {
        margin-bottom: 16px;
        width: 100%;
        padding-bottom: 100%;
        border-radius: 20px;
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
    }
    &__title {
        margin-bottom: 10px;
        font-family: $ff-title;
        font-weight: 500;
        font-size: 20px;
        line-height: 1.2;
        position: relative;
        &::after {
            content: "";
            position: absolute;
            top: 100%;
            left: 0;
            width: 59px;
            height: 1px;
            background-color: $primary;
        }
    }
    &__text {
        flex-grow: 1;
        margin-bottom: 16px;
        font-family: $ff-text;
        font-weight: 300;
        font-size: 18px;
        color: $gray;
    }
}