.gallery {
    background-image: url(../img/design3.jpg);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    padding: 47px 0 134px;
    overflow-x: hidden;
    @include md-max {
        padding: 24px 0 47px;
    }
    &__top {
        margin-bottom: 40px;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
    }
    .main-title {
        max-width: 330px;
        @include sm-max {
            max-width: 200px;
            font-weight: 500;
            font-size: 26px;
        }
    }
    &__body {
        display: flex;
        flex-direction: column;
    }
    &__list {
        margin-right: -200px;
        display: flex;
    }
    &__nav {
        display: flex;
        .slick-arrow {
            cursor: pointer;
            &:not(:last-child) {
                margin-right: 30px;
              }
        }
    }
    &__item {
        &:not(:last-child) {
            margin-right: 58px;
        }
    }
}
.gallery-card {
    max-width: 262px;
    &__img {
        margin-bottom: 16px;
        width: 100%;
        padding-bottom: 100%;
        border-radius: 20px;
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
    }
    &__title {
        margin-bottom: 10px;
        font-family: $ff-title;
        font-weight: 500;
        font-size: 20px;
        line-height: 1.2;
        position: relative;
        &::after {
            content: "";
            position: absolute;
            top: 100%;
            left: 0;
            width: 59px;
            height: 1px;
            background-color: $primary;
        }
    }
    &__text {
        flex-grow: 1;
        margin-bottom: 16px;
        font-family: $ff-text;
        font-weight: 300;
        font-size: 18px;
        color: $gray;
    }
}