.pricing {
  overflow-x: hidden;
  &__list {
    padding-top: 40px;
    padding-bottom: 50px;
  }
}
.pricing-header {
  &__title {
    max-width: 70%;
    padding-top: 138px;
    padding-bottom: 44px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: relative;
    @include lg-max {
      padding: 0;
      margin: 23px 0;
      min-height: 250px;
    }
    @include md-max {
      min-height: 174px;
    }
    &:after {
      content: "";
      margin-left: 88px;
      width: 442px;
      height: 442px;
      position: absolute;
      left: 100%;
      top: 0;
      background-image: url(../img/design8.png);
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
      @include lg-max {
        margin-left: 20px;
        width: 250px;
        height: 250px;
      }
      @include md-max {
        width: 174px;
        height: 174px;
      }
    }
  }
  &__text {
    max-width: 70%;
    @include lg-max {
      max-width: 100%;
    }
  }
  .main-title {
    @include sm-max {
      font-size: 26px;
    }
  }
}
.pricing-list {
  padding-top: 50px;
  display: flex;
  margin: 0 -15px;
  @include md-max {
    flex-direction: column;
  }
  &__item {
    padding: 0 15px;
    width: 33.3333%;
    @include md-max {
      width: 100%;
    }
    &:not(:last-child) {
      @include lg-max {
        margin-bottom: 50px;
      }
    }
  }
}
.line-block {
  margin: 15px 0;
  padding: 22px 0;
  border-top: 1px solid $primary;
  border-bottom: 1px solid $primary;
  font-family: $ff-title;
  font-weight: 300;
  font-size: 20px;
}
.pricing-card {
  &__title {
    padding: 17px 40px;
    border-radius: 26px;
    font-family: $ff-title;
    font-size: 20px;
    font-weight: 500;
    text-transform: uppercase;
    color: white;
    text-align: center;
    position: relative;
    &::before,
    &::after {
      content: "";
      position: absolute;
      top: 0;
      width: 40px;
      height: 100%;
      background-image: url(../img/pricing-bg.svg);
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
    }
    &::before {
      left: 0;
      transform: rotate(180deg);
    }
    &::after {
      right: 0;
    }
  }
  &__item,
  &__subitem {
    padding: 9px 10px;
    font-family: $ff-text;
    font-size: 18px;
    font-weight: 300;
    line-height: 23px;
    display: flex;
    justify-content: space-between;
  }
  &__item:not(:last-child) {
    border-bottom: 1px solid $primary;
  }
  &__subitem {
    color: #707070;
  }
  &__value {
    color: #707070;
  }
  &__option {
    span {
      color: $primary;
    }
  }
  &__btn {
    margin-top: 30px;
  }
}
.pricing-dropdown {
  margin: 15px 0;
  &__item {
    border-top: 1px solid $primary;
    &:last-child {
      border-bottom: 1px solid $primary;
    }
  }
  &__trigger {
    padding: 23px 20px 23px 0;
    font-family: $ff-title;
    font-weight: 300;
    font-size: 20px;
    line-height: 24px;
    display: block;
    position: relative;
    &:after {
      content: "";
      width: 15px;
      height: 15px;
      background-color: transparent;
      border-left: 1px solid #3a2c48;
      border-bottom: 1px solid #3a2c48;
      position: absolute;
      right: 0;
      top: 50%;
      transform: translate(0, -50%) rotate(-45deg);
      cursor: pointer;
      transition: all 0.3s ease;
    }
    &:hover::after {
      border-color: $primary;
    }
  }
  &__inner {
    padding: 20px;
    font-family: $ff-text;
    font-weight: 300;
    font-size: 18px;
    line-height: 27px;
    color: #000;
    p, li {
      margin-bottom: 23px;
    }
  }
}
