.contact-us {
  background-image: url(../img/design4.png);
  background-position: center 60px;
  background-size: 50%;
  background-repeat: no-repeat;
  @include md-max {
    background-size: 80%;
    background-position: center 50px;
  }
  &__title {
    margin: 0 0 7px;
    font-family: $ff-title;
    font-size: 44px;
    line-height: 1.2;
    font-weight: 500;
    text-transform: uppercase;
    @include lg-max {
      font-size: 40px;
    }
    @include md-max {
      text-align: center;
    }
    @include sm-max {
      font-size: 26px;
    }
  }
  &__list {
    margin: 20px -50px 78px;
    display: flex;
    @include md-max {
      flex-direction: column;
      margin: 20px 0 70px;
    }
  }
  &__item {
    width: 50%;
    padding: 0 50px;
    @include md-max {
        margin-bottom: 10px;
      width: 100%;
      padding: 0;
      text-align: center;
    }
  }
}
.contact-block {
  padding: 28px 0;
  font-family: $ff-text;
  font-size: 18px;
  font-weight: 300;
  line-height: 27px;

  &:not(:first-child) {
    border-top: 1px solid $primary;
  }
  &__title {
    margin-bottom: 12px;
    font-family: $ff-title;
    font-size: 24px;
    font-weight: 500;
    line-height: 29px;
    text-transform: uppercase;
  }
  &__subtitle {
    margin-bottom: 4px;
    font-family: $ff-text;
    font-size: 18px;
    font-weight: 500;
    line-height: 27px;
  }
  &__row {
    &:not(:last-child) {
      margin-bottom: 10px;
    }
  }
  &__map {
    margin-top: 23px;
    width: 100%;
    padding-bottom: 52%;
    position: relative;
    iframe,
    object,
    embed {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
}

.contact-us-form {
  padding: 31px 0 38px;
  background-color: $secondary;
  color: #fff;
  &__container {
    max-width: 540px;
    margin: 0 auto;
    padding: 0 20px;
  }
  &__title {
    margin-bottom: 30px;
    font-family: $ff-title;
    font-size: 36px;
    font-weight: 300;
    text-align: center;
    text-transform: uppercase;
    @include lg-max {
        font-size: 30px;
    }
    @include sm-max {
        font-size: 20px;
    }
  }
  .text-group {
    padding: 0 0 20px;
  }
  .btn {
    display: block;
    margin: 20px auto 0;
  }
}
