.hero {
  min-height: 100vh;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
  align-items: flex-end;
  @include md-max {
    min-height: auto;
    height: 100vh;
    max-height: 700px;
  }
  &__content {
      flex-grow: 1;
    width: 100%;
    max-width: 1040px;
    margin: 0 auto;
    padding: 100px 20px 50px;
  }
  &__title {
      margin: 0 0 7px;
      font-family: $ff-title;
      font-size: 60px;
      font-weight: 500;
      color: #E7BB90;
      text-transform: uppercase;
      @include lg-max {
        font-size: 44px;
        line-height: 1.2;
      }
      @include sm-max {
        font-size: 30px;
      }
  }
  &__subtitle {
    margin: 0 0 7px;
    font-family: $ff-title;
    font-size: 44px;
    font-weight: 300;
    color: #fff;
    text-transform: uppercase;
    @include lg-max {
      font-size: 36px;
      line-height: 1.2;
    }
    @include sm-max {
      font-size: 22px;
    }
  }
}
