//select
.group-select .select2-container.select2-container--default {
  min-width: 5.5rem;
}
.select2-container--default .select2-selection--single.select-light {
  background-color: transparent;
  border: none;
  border-radius: 0;
}
.select2-container--default
  .select2-selection--single
  .select2-selection__arrow
  b {
  border: none;
  width: 9px;
  height: 6px;
  background-image: url(../img/icons/icon-down.svg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.select2-container--default
  .select2-selection--single
  .select2-selection__rendered {
  color: $accent;
  font-family: $ff-title;
  font-size: 1.6rem;
}
.select2-dropdown.select-light-dropdown {
  border: none;
  color: $accent;
  font-size: 1.4rem;
}
.select2-container--default
  .select-light-dropdown
  .select2-results__option--highlighted[aria-selected] {
  background-color: $accent;
}
//input
::-webkit-input-placeholder {
  color: #ffffff9f;
}
::-moz-placeholder {
  color: #ffffff9f;
}
:-ms-input-placeholder {
  color: #ffffff9f;
}
:-moz-placeholder {
  color: #ffffff9f;
}

.form-row {
  display: flex;
  margin: 0 -13px;
  @include lg-max {
    margin: 0 -5px;
  }
}
.text-group {
  padding: 5px 13px;
  flex-grow: 1;
  @include lg-max {
    padding: 5px;
  }
  input,
  textarea {
    width: 100%;
    max-width: 100%;
    padding: 18px 26px;
    background-color: transparent;
    border-radius: 26px;
    border: 1px solid #fff;
    font-size: 13px;
    color: #fff;
    resize: none;
  }
}
.text-group {
  .input-light {
    width: 100%;
    padding: 8px 0;
    color: #fff;
    background-color: transparent;
    font-family: $ff-text;
    font-size: 13px;
    line-height: 20px;
    border: none;
    border-bottom: 1px solid $primary;
    border-radius: 0;
  }
}
