.video-gallery {
  padding-bottom: 74px;
  @include md-max {
    padding-bottom: 40px  ;
  }
  &__header {
    margin-bottom: 60px;
  }
  &__top {
    padding-bottom: 18px;
    width: 100%;
  }
  &__body {
    margin: 0 -6px;
    display: flex;
    flex-wrap: wrap;
  }
  &__item {
    padding: 18px 6px;
    width: 33.33333%;
    @include lg-max {
      width: 50%;
    }
    @include sm-max {
      width: 100%;
    }
  }
}
.video-container {
  position: relative;
  padding-bottom: 56.25%;
  padding-top: 30px;
  height: 0;
  overflow: hidden;
}
.video-container iframe,
.video-container object,
.video-container embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
