.contact-banner {
    padding: 39px 0;
    background-color: $secondary;
    &__title {
        margin-bottom: 27px;
        font-family: $ff-title;
        font-size: 44px;
        font-weight: 300;
        text-transform: uppercase;
        text-align: center;
        color: #fff;
        strong {
            font-weight: 500;
        }
        @include lg-max {
            margin-bottom: 31px;
            font-size: 26px;
        }
    }
    &__buttons {
        display: flex;
        align-items: center;
        justify-content: center;
        @include rwd-max(370) {
            flex-wrap: wrap;
        }
        .btn {
            flex-shrink: 0;
            padding: 17px 15px;
            margin: 0 16px;
            @include md-max {
                margin: 5px 8px;
                min-width: 149px;
            }
        }
    }
}