.sidebar-form {
  padding: 21px 13px;
  width: 350px;
  position: absolute;
  top: -70px;
  right: 0;
  background-color: $secondary;
  color: #fff;
  border-radius: 20px;
  @include lg-max {
    padding: 21px 24px;
    position: relative;
    top: 0;
    width: 100vw;
    border-radius: 0;
    margin: 40px -20px 0;
    left: 0;
  }
  &__title {
    margin-bottom: 30px;
    font-family: $ff-title;
    font-size: 20px;
    font-weight: 300;
    text-align: center;
    text-transform: uppercase;
  }
  .text-group {
    padding: 0 20px 20px;
    @include lg-max {
      padding: 0 0 20px;
    }
  }
}

