.compounds {
  padding-bottom: 50px;
  overflow-x: hidden;
  &__block {
    display: flex;
    @include lg-max {
        flex-direction: column;
    }
  }
  &__content {
    padding-right: 40px;
    width: 45%;
    @include lg-max {
        padding-right: 0;
        width: 100%;
    }
  }
  &__text {
      margin-top: 26px;
  }
  &__img {
      width: 55%;
      @include lg-max {
          margin-top: 50px;
        width: 100%;
    }
  }
}
.compound-gallery {
  margin-right: -500px;
  &__item {
    width: 575px;
    @include sm-max {
        width: 262px;
    }
    &:not(:last-child) {
      margin-right: 38px;
      @include md-max {
          margin-right: 20px;
      }
    }
  }
  &__img {
    width: 100%;
    padding-bottom: 77%;
    border-radius: 20px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    @include sm-max {
        padding-bottom: 107%;
    }
  }
  &__nav {
    margin-top: 35px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    .slick-arrow {
      cursor: pointer;
        &:not(:last-child) {
            margin-right: 30px;
          }
    }
  }
}
