.about {
  padding-bottom: 76px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  &--decor {
    background-image: url(../img/design5.jpg);
    @include sm-max {
      background-image: url(../img/bg-2.png);
    }
  }
  &__decor {
    height: 172px;
    margin-top: -7px;
    margin-bottom: 9px;
    background-position: center bottom;
    background-size: cover;
    background-repeat: no-repeat;
    @include lg-max {
      height: 128px;
      margin-top: 0;
      margin-bottom: 47px;
    }
  }
  &__header {
    padding-bottom: 20px;
  }
  &__columns {
    display: flex;
    @include lg-max {
      flex-direction: column;
    }
  }
  &__column {
    width: 50%;
    @include lg-max {
      width: 100%;
    }
    &:last-child {
      padding-left: 20px;
      @include lg-max {
        padding: 0;
      }
    }
  }
  &__content {
    margin-bottom: 22px;
    max-width: 500px;
    font-family: $ff-text;
    font-weight: 300;
    font-size: 20px;
    line-height: 1.5;
    @include lg-max {
      max-width: 100%;
    }
    p:not(:last-child) {
      margin-bottom: 30px;
    }
  }
  &__img {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    img {
        width: 100%;
    }
  }
  &__note {
      padding: 10px 0;
      position: absolute;
      top: 100%;
      left: 0;
      font-size: 18px;
  }
  &--center {
    .about__columns {
        align-items: center;
    }
  }
}
.img-card {
  background-image: url(../img/design4.png);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  width: 100%;
  height: 100%;
  padding-top: 20%;
  padding-bottom: 20%;
  img {
    display: block;
    width: 55%;
    border-radius: 50%;
    object-fit: cover;
  }
  &__title {
      margin-top: -15%;
    text-align: center;
  }
  &__name {
    font-family: $ff-title;
    font-size: 20px;
    font-weight: 500;
    line-height: 1.2;
  }
  &__possition {
    font-family: $ff-text;
    font-size: 21px;
    font-weight: 300;
    color: #707070;
    @include md-max {
        font-size: 18px;
    }
  }
}
