.features {
  margin: 130px auto 83px;
  position: relative;
  width: 100%;
  max-width: 1310px;
  display: flex;
  position: relative;
  @include lg-max {
    margin: 50px auto;
  }
  @include md-max {
    flex-direction: column-reverse;
  }
  &__img {
    padding-left: 20px;
    position: absolute;
    max-width: 50%;
    height: 100%;
    right: 0;
    bottom: 0;
    @include lg-max {
      padding-top: 240px;
    }
    @include md-max {
      margin: 0 auto 45px;
      position: relative;
      padding: 0;
      max-width: 100%;
    }
    img {
      border-radius: 20px;
      overflow: hidden;
      height: 100%;
      object-fit: cover;

    }
  }
  &__title {
    max-width: 50%;
    font-family: $ff-title;
    font-weight: 500;
    font-size: 44px;
    line-height: 1.16;
    color: #000;
    color: #000;
    text-transform: uppercase;
    position: relative;
    margin-bottom: 40px;
    display: flex;
    align-items: center;
    @include lg-max {
      max-width: 100%;
    }
  }
  &__number {
    min-width: 80px;
    font-family: "Raleway", "Arial", sans-serif;
    font-size: 115px;
    font-weight: 300;
    color: #9C8F63;
    display: block;
    margin-top: -25px
  }
  &__text {
    display: block;
    max-width: 50%;
    margin-bottom: 40px;
    font-family: $ff-text;
    font-size: 20px;
    font-weight: 300;
    line-height: 1.5;
    color: #000;
    @include lg-max {
      max-width: 100%;
    }
  }
  &__list {
    max-width: 50%;
    list-style: none;
    margin-top: 40px;
    margin-bottom: 40px;
    @include md-max {
      margin: 80px auto 40px;
      max-width: 100%;
      display: flex;
      flex-direction: column;
    }
    @include sm-max {
      align-items: center;
      justify-content: center;
    }
  }
  &__item {
    display: flex;
    align-items: center;
    font-family: $ff-title;
    font-size: 24px;
    line-height: 1.2;
    font-weight: 500;
    color: #000;
    @include lg-max {
      font-size: 20px;
    }
    @include sm-max {
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
    &:not(:last-child) {
      margin-bottom: 58px;
    }
  }
  &__icon {
    min-width: 100px;
    margin-right: 22px;
  }
  &__btn {
    @include sm-max {
     display: flex;
     align-items: center;
     justify-content: center;
    }
  }
}
