.location {
    background-color: $secondary;
    min-height: 432px;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    &__header {
        width: 50%;
        @include md-max {
            width: 60%;
        }
    }
    &__map {
        position: absolute;
        top: 0;
        right: 0;
        height: 100%;
        width: 50%;
        background-color: #fff;
        @include md-max {
            width: 36%;
            background-color: transparent;
        }
    }
}